import axios from 'axios';
import Server_connection from '../../constants/constants';

export const register = newUser => {
    return axios
    .post(Server_connection.ADMIN_SIGN_UP_URL, {
        username: newUser.username,
        email: newUser.email,
        password: newUser.password
    })
    .then(res => {
        console.log("Registered");
        return res.data;
    })
}

export const login = user => {
    return axios
    .post(Server_connection.ADMIN_LOGIN_URL, {
        email: user.email,
        password: user.password
    })
    .then(res => {
        localStorage.setItem('IntellUserToken', res.data);
        return res.data;
    })
    .catch(err => {
        console.log(err)
    })
}