import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { Button, Card, CardBody, CardGroup, Col, Container, Form, Input, InputGroup, InputGroupAddon, InputGroupText, Row } from 'reactstrap';
import './Login.scss';
import logo from '../../../assets/img/brand/di2.png'
import { login } from '../../Pages/UserFuctions'
import ForgotPassword from '../../Settings/ForgotPassword'
import Spinner from '../../Loaders/Spinner'

class Login extends Component {
  constructor(props) {
    super(props);
    this.state = { 
        email: '',
        password: '',
        message: '',
        forgotPasswordModalShow: false,
        isLoaded: false
    };

    this.onSubmit = this.onSubmit.bind(this);
    this.onChange = this.onChange.bind(this);
  }

  onChange(e){
    this.setState({[e.target.name]: e.target.value});
  }

  onSubmit(e){
    e.preventDefault();

    this.setState({isLoaded : true})

    const user = {
      email: this.state.email,
      password: this.state.password
    }

    login(user).then(res => {
      if(res){
        this.setState({message : res.message, isLoaded : false})
        console.log([this.state.message])
        if([this.state.message] == 'Email or password is incorrect'){
          this.props.history.push('/login')
        }
        else if([this.state.message] == 'User does not exist'){
          this.props.history.push('/login')
        }
        else{
          this.props.history.push('/dashboard')
          window.location.reload();
        }
      }else{
        this.props.history.push('/login')
      }
    })
  }
  
  render() {
    var { isLoaded } = this.state;
    let forgotPasswordModalClose = () => this.setState({forgotPasswordModalShow : false});

    return (
      <div className="app flex-row align-items-center">
        <Container>
          <Row className="justify-content-center">
            <Col md="5">
              <CardGroup>
                <Card className="p-4">
                  <CardBody>
                    <img src={logo} className="imgLogo img-avatar" alt="" width= "140" height="50"/>
                    <Form>
                      {/* <h3>Login</h3> */}
                      <h6 className = 'erro-msg'>{isLoaded ? <h4>loading..</h4> : this.state.message }</h6><hr/>
                      {/* <p className="text-muted">Sign In</p> */}
                      <InputGroup className="mb-3">
                        <InputGroupAddon addonType="prepend">
                          <InputGroupText>
                            <i className="fa fa-envelope-o"></i>
                          </InputGroupText>
                        </InputGroupAddon>
                        <Input type="text" placeholder="Email" autoComplete="email" name="email"
                        value={this.state.email} onChange={this.onChange}/>
                      </InputGroup>
                      <InputGroup className="mb-4">
                        <InputGroupAddon addonType="prepend">
                          <InputGroupText>
                            <i className="icon-lock"></i>
                          </InputGroupText>
                        </InputGroupAddon>
                        <Input type="password" placeholder="Password" autoComplete="current-password" name="password"
                        value={this.state.password} onChange={this.onChange}/>
                      </InputGroup>
                      <Row>
                        <Col xs="6">
                          <Button color="primary" className="px-4 btn-pill" onClick={this.onSubmit}>Login</Button>
                        </Col>
                        <Col xs="6" className="text-right">
                        <Button color="link" className="px-0" onClick={() => this.setState({forgotPasswordModalShow : true})}>Forgot password?</Button>
                        </Col>
                      </Row>
                    </Form>
                  </CardBody>
                </Card>
              </CardGroup>
            </Col>
          </Row>
        </Container>
        <ForgotPassword
          show = {this.state.forgotPasswordModalShow}
          onHide = {forgotPasswordModalClose}/>
      </div>
    );
  }
}

export default Login;
